<template>
  <div class="chatbox-container">
    <div class="container">
      <h1>의상현 Bot V1</h1>
      <div class="messageBox mt-8" ref="messageBox">
        <template v-for="(message, index) in messages" :key="index">
          <div :class="message.from == 'user' ? 'messageFromUser' : 'messageFromChatGpt'">
            <div :class="message.from == 'user' ? 'userMessageWrapper' : 'chatGptMessageWrapper'">
              <div :class="message.from == 'user' ? 'userMessageContent' : 'chatGptMessageContent'">{{ message.data }}</div>
            </div>
          </div>
        </template>
      </div>
      <div class="inputContainer">
        <input
          v-model="currentMessage"
          @keyup.enter="sendMessage"
          type="text"
          class="messageInput"
          placeholder="Ask me anything..."
        />
        <button @click="sendMessage" class="askButton">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke-width="2"
            stroke="currentColor"
            class="w-6 h-6"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              d="M4.75 19.25l14.5-7.25-14.5-7.25v5.5L14 12l-9.25 4.75v2.5z"
            />
          </svg>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ChatBox',
  data() {
    return {
      currentMessage: '',
      messages: [],
    };
  },
  methods: {
    async sendMessage() {
      if (!this.currentMessage.trim()) return;

      this.messages.push({
        from: 'user',
        data: this.currentMessage,
      });

      const loadingMessage = {
        from: 'gpt-doctor',
        data: '...',
      };

      this.messages.push(loadingMessage);

      const messageToSend = this.currentMessage;
      this.currentMessage = '';
      this.$nextTick(() => {
        this.scrollToBottom();
      });

      try {
        const response = await axios.post('https://asia-northeast3-profound-keel-395608.cloudfunctions.net/gpt-doctor-v1', {
          message_user: messageToSend,
          session_id: 'test',
        });

        this.messages.splice(this.messages.indexOf(loadingMessage), 1, {
          from: 'gpt-doctor',
          data: response.data.message_asst,
        });

      } catch (error) {
        this.messages.splice(this.messages.indexOf(loadingMessage), 1, {
          from: 'gpt-doctor',
          data: 'Sorry, there was an error processing your request.',
        });
      }

      this.$nextTick(() => {
        this.scrollToBottom();
      });
    },
    scrollToBottom() {
      const messageBox = this.$refs.messageBox;
      messageBox.scrollTop = messageBox.scrollHeight;
    },
  },
};
</script>

